import { NzGridModule } from "app/showcase/share/nz-grid/nz-grid.module";
import { NzToolTipModule } from "app/showcase/share/nz-tooltip/nz-tooltip.module";
import { ShipperSelectDemoBasicComponent } from "app/showcase/custom-components/shipper-select-demo/shipper-select-demo-basic.component";
import { ShipperSelectDemoObjectComponent } from "app/showcase/custom-components/shipper-select-demo/shipper-select-demo-object.component";

@NgModule({
	declarations:[
		ShipperSelectDemoComponent,
		ShipperSelectDemoBasicComponent,
		ShipperSelectDemoObjectComponent
	],
	imports: [
		ShipperSelectDemoRoutingModule,
		CommonModule,
		FormsModule,
		NzGridModule,
		NzToolTipModule,
		NzCodeBoxModule,
		CneeSelectModule
	]
})
export class ShipperSelectDemoComponentModule { }



