import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'shipper-select-demo-object',
  template: `
  <cnee-select width="150px" defaultLabel="请选择…" [(ngModel)]="cnee" valueField="object" (onChange)="onChange($event)"></cnee-select>
  <span>{{cneeObj}}</span>
  `,
  styles: []
})
export class ShipperSelectDemoObjectComponent implements OnInit {

  cnee: Object;
  cneeObj: any;

  constructor() { }

  ngOnInit() {
  }

  onChange($event){
      this.cneeObj=JSON.stringify($event);
  }
}



